@import url(//fonts.googleapis.com/css?family=Open+Sans);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
* {
  font-family: "AirbnbCereal", "Open Sans";
  margin: 0;
  padding: 0;
}

body,
html {
  height: 100%;
}
nav.navbar {
  box-shadow: 0px 4px 10px rgba(0, 0, 0, 0.1);
}

.nav-link.a:hover::after {
  content: "";
  display: block;
  border-bottom: 3px solid #5bc0de;
  margin: 0 auto -3px;
}

.nav-link.a.active::after {
  content: "";
  display: block;
  border-bottom: 3px solid #5bc0de;
  margin: 0 auto -3px;
}

.card{
    border:none;
    margin-bottom: -60px;
    background: transparent;
}

.card img{
    border-radius: 10px 25px;
    box-shadow: -10px 10px 7px rgba(0, 0, 0, .3);
    height: 11rem;
    height: 145px;
}

.card .card-body button{
    position: relative;
    float: right;
    top: -63px;
    right: -10px;
    padding: 4% 9%;
    font-size: .7em;
}

.card .card-body .card-title
{ 
    position: relative;
    top: -15px;
    font-weight: bolder;
}
.products {
  min-height: 71vh;
}

.products,
.table-dash {
  margin-top: 20px;
  background: white;
}

.table-dash table th,
.table-dash table td {
  text-align: center;
}

.table-dash img {
  width: 42px;
  height: 23px;
}

table.table td button {
  font-size: 11px;
  padding: 3px 12px;
}

.secure-img {
  background-image: url(/POS-APP-Using_ReactJS/static/media/secure.897aa1ea.jpg);
  height: 500px;
  width: 100%;
  margin-top: -350px;
  background-repeat: no-repeat;
  background-size: cover;
}

.nav-product {
  background: transparent;
  margin-bottom: -5px;
  border-left: 5px solid #5bc0de;
}

.nav-product.a {
  background: transparent;
  margin-top: 20px;
  margin-bottom: -5px;
  border-left: 5px solid #5bc0de;
}

.nav-product .nav-link {
  color: #5bc0de;
  font-size: 14px;
  font-family: "Open Sans";
  font-weight: bolder;
}

.cool {
  background-image: url(/POS-APP-Using_ReactJS/static/media/coffee.877635d8.png);
  position: fixed;
  top: 0;
  bottom: 0;
  right: 0;
}

